/*
  Create infinite animated moving background
  Background should be gradient from white to gray

*/

.skeleton {
  background: linear-gradient(
    to right,
    #cecece 0%,
    #f3f3f3 10%,
    #e5e5e5 100%
  );
  -webkit-animation: skeleton 2s infinite linear;
          animation: skeleton 2s infinite linear;
  width: auto;
  height: auto;
}

@-webkit-keyframes skeleton {
  0% {
    background-position: -100px 0;
  }

  100% {
    background-position: 100% 0;
  }
}

@keyframes skeleton {
  0% {
    background-position: -100px 0;
  }

  100% {
    background-position: 100% 0;
  }
}

